main {
    background-image: url('/deploy_angular2/IMG_9166.jpg');
    background-repeat: no-repeat; 
    background-size: cover;
}
/*Set the background color of dark-mode to black 
  and remove the background photo.*/
div.dark-mode main{
    background-image: none;
    background-color: black;
    color: white;
}
/*Set the background of some elements to grey.*/
div.dark-mode header,
div.dark-mode footer,
div.dark-mode tr:nth-child(odd),
div.dark-mode .about-content {
    background-color: grey;
}
div.dark-mode .close {
    background-color: black;
}
header{
    background-color: blue;
    color:#fff;
    /* Make header a flexbox*/
    display: flex; 
    /* Wrap the content */
    flex-wrap: wrap; 
    font-size:1.5em;
    /*Place the 2nd div (heading) close to the center of header,
      place the 3rd div (radio buttons and info icon) to the 
      right hand side of the header*/
    /*If this way is not the best way, please teach me*/
    justify-content: space-between; 
    text-align: center;
}
footer{
    background-color: #eee;
    font-size: 1em;
    text-align: center;
}
.flexbox {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}
.col1{
    flex:1 1 0; /*grow, shrink, basis*/
    min-width: 400px;

}

.col2{
    flex:1 1 0; /*grow, shrink, basis*/
    min-width: 400px;
    min-height:400px;

}
table {
    border-collapse: collapse;
    display: block;
    font-size: 1em;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    width: 100%;
}
td {
    border: 2px solid blue;
    font-size: 1em;
    width: 50vw;
} 
th {
    border: 2px solid blue;
    font-size: 1.2em;
    width: 50vw;
} 
/*Only single row has white background*/
tr:nth-child(odd) {
    background-color: white;
}
.about {
    /* Center vertically */
    align-items: center; 
    /* Make this layer semi-transparent of black */
    background-color: rgba(30, 30, 30, 0.9); 
    display: flex;
    height: 100%;
    /* Center horizontally */
    justify-content: center; 
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    /* Make it the layer between main and about-content*/
    z-index: 1; 
}
.about-content {
    background-color: white;
    border: 1px solid blue;
    border-radius: 5px;
    height: 30%; 
    /* Make it vertically scrollable */
    overflow-y: auto;
    text-align: center;
    width: 50%;
    /* Make it the top layer */
    z-index: 2; 
}
.close {
    background-color: blue;
    color: white;
    font-size: 1em;
    font-weight: bold;
}

